const getters = {

    getBankTypeByName (state, payload) {
        return payload => state.bankaccounttypes.find( function( e ){
            return e.value === payload;
        });
    },
    getBankTypes (state) {
        return  state.bankaccounttypes;
    }
    
}



export default {
    state,
    getters
}


