const getters = {
    getMethodDetails (state) {
        var item = state.paymentmethod.find( item => item.name == ( state.selectedmethod == '' ? state.defaultmethod :  state.selectedmethod ));
        return item;
    }
}

const mutations = {
    selectMethod (state, payload ) {
        state.selectedmethod = payload;
    }
}

export default {
    state,
    getters,
    mutations
}


